const menuItems = {
    home: {value: 'home', icon: <Home/>},
    files: {value: 'files', icon: <CloudUpload/>},
    researches: {value: 'researches', icon: <Research/>},
    people: {value: 'people', icon: <People/>},
    settings: {value: 'settings', icon: <Settings/>},
    logout: {value: 'logout', icon: <Logout/>},
    login: {value: 'login', icon: <Login/>},
}

const authorizedOptions = ['home', 'files', 'people', 'researches', 'settings', 'logout'];
const unauthorizedOptions = ['home', 'login'];

const isCollapsed = () => window.innerWidth < 750;

export const HeaderMenu = ({isAuthorized, onChange, value}) => {
    const [anchorEl, setAnchorEl] = useState(null);
    const [collapsed, setCollapsed] = useState(isCollapsed());

    useEffect(() => {
        window.addEventListener('resize', windowSizeCheck);
        return () => window.removeEventListener('resize', windowSizeCheck);
    }, [])

    const windowSizeCheck = () => setCollapsed(isCollapsed())

    useEffect(() => {
        if (anchorEl) setAnchorEl(null);
    }, [value])

    const renderTabs = (tabs, noIndicator = false) => (
        <Tabs
            value={value}
            indicatorColor="primary"
            textColor="primary"
            onChange={(_, value) => onChange(value)}
            variant='scrollable'
            style={{display: 'flex'}}
            TabIndicatorProps={noIndicator ? {style: {display: 'none'}} : {}}
        >
            {tabs.map((name, id) => (
                <Tab icon={menuItems[name].icon} value={menuItems[name].value} style={{minWidth: 64}} key={id}/>
            ))}
        </Tabs>
    )

    const containerCommonStyle = {
        display: 'flex',
        flexDirection: 'row',
        justifyContent: 'space-between',
        alignItems: 'center',
    }
    const containerWideStyle = {
        padding: '0px 8px',
        borderRadius: '0px 0px 8px 8px',
    }

    const containerStyle = collapsed ? containerCommonStyle : {...containerCommonStyle, ...containerWideStyle};

    return (
        <Paper square style={containerStyle}>
            <Box marginX={2} width={200} style={{minWidth: 200}}>
                <Typography variant={'h6'}>
                    DICOM Share Viewer
                </Typography>
            </Box>
            {isAuthorized ? (
                <Fragment>
                    {collapsed ? (
                        <Fragment>
                            <IconButton onClick={(event) => setAnchorEl(event.currentTarget)}>
                                <MenuIcon/>
                            </IconButton>
                            <Menu
                                anchorEl={anchorEl}
                                keepMounted
                                open={Boolean(anchorEl)}
                                onClose={() => setAnchorEl(null)}
                                getContentAnchorEl={null}
                                transformOrigin={{ vertical: 'top', horizontal: 'right' }}
                                anchorOrigin={{ vertical: -40, horizontal: 48 }}
                            >
                                <Box style={{margin: '-8px 8px'}}>
                                    {renderTabs(authorizedOptions, true)}
                                </Box>
                            </Menu>
                        </Fragment>
                    ) : (
                        <Fragment>{renderTabs(authorizedOptions)}</Fragment>
                    )}
                </Fragment>
            ) : (
                <Fragment>{renderTabs(unauthorizedOptions)}</Fragment>
            )}
        </Paper>
    )
}