import React, {useState} from "react";
import {Box, Paper, Typography, Stepper, Step, StepLabel} from "@material-ui/core";
import {Alert} from "@material-ui/lab";

export const InDevelop = ({title, features}) => {
    const [step, setStep] = useState(0)

    return (
        <Paper>
            <Box paddingX={4} paddingY={3}>
                <Typography variant={'h5'}>{title}</Typography>
                {features ? (
                    <Box marginY={2}>
                        <Stepper orientation="vertical" activeStep={step}>
                            {features.map((feature, id) => (
                                <Step key={id} onClick={() => setStep(id)}>
                                    <StepLabel>{feature}</StepLabel>
                                </Step>
                            ))}
                        </Stepper>
                    </Box>
                ) : null}
                <Box marginY={2}>
                    <Alert color={'info'}>Страница находится в разработке</Alert>
                </Box>
            </Box>
        </Paper>
    )
}