import React, {useState} from 'react'
import {createTheme, MuiThemeProvider} from "@material-ui/core/styles";
import {Box, Paper,  Typography, CssBaseline} from "@material-ui/core";
import {HeaderMenu} from "./HeaderMenu";
import {HomePage} from "./HomePage";
import {InDevelop} from "./InDevelop";


export const Main = () => {
    const [page, setPage] = useState('home');


    return (
        <MuiThemeProvider theme={createTheme({palette: {type: 'light'}})}>
            <CssBaseline />
            <Box>
                <Box width={'100%'} marginBottom={2}>
                    <HeaderMenu isAuthorized={true} value={page} onChange={setPage}/>
                </Box>
                <Box padding={1}>
                    {page === "home" ? (<HomePage />) : null}
                    {page === "files" ? (
                        <InDevelop
                            title={'Файлы пользователя'}
                            features={['Сохранение файлов на сервер']}
                        />
                    ) : null}
                    {page === "people" ? (
                        <InDevelop
                            title={'Контакты'}
                            features={[
                                'Список контактов пользователя',
                                'Раздел заявок на добавление в контакты',
                                'Поиск контактов (студентов, кураторов, врачей)'
                            ]}
                        />
                    ) : null}
                    {page === "researches" ? (
                        <InDevelop
                            title={'Исследования'}
                            features={[
                                'Проведение групповых и одиночных исследований',
                                'Форма создания нового исследования',
                                'Страницы исследований'
                            ]}
                        />
                    ) : null}
                    {page === "settings" ? (
                        <InDevelop
                            title={'Настройки'}
                            features={['Смена пароля', 'Настройки приватности']}
                        />
                    ) : null}
                    {page === "logout" ? (<InDevelop title={'Авторизация'}/>) : null}
                </Box>
            </Box>
        </MuiThemeProvider>
    )
}